@import url("https://fonts.googleapis.com/css2?family=Karla:wght@400;700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=OptimaLTStd:ital,wght@0,400;1,400&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Optima+LT+Std:ital,wght@0,400;1,400&display=swap");
body {
  margin: 0;
  line-height: normal;
}
:root {
  /* fonts */
  --font-optima-lt-std: "Optima LT Std";
  --font-karla: Karla;
  --font-optimaltstd: OptimaLTStd;

  /* font sizes */
  --font-size-sm: 14px;
  --font-size-base: 16px;
  --font-size-xl: 20px;
  --font-size-5xl: 24px;

  /* Colors */
  --white: #fff;
  --color-whitesmoke: #f4f4f2;
  --grey: #65635f;
  --grey-20: rgba(101, 99, 95, 0.2);
  --black: #161412;
  --color-gray-100: rgba(22, 37, 51, 0.6);

  /* Gaps */
  --gap-5xs: 8px;
  --gap-594xl: 613px;
  --gap-3xs: 10px;

  /* Paddings */
  --padding-37xl: 56px;
  --padding-85xl: 104px;
}
