.wall-mockup-1-icon {
  position: relative;
  width: 1440px;
  height: 900px;
  object-fit: cover;
  z-index: 0;
}
.shadow {
  position: absolute;
  height: 79.17%;
  width: 87.07%;
  top: 17.42%;
  right: 6.9%;
  bottom: 3.41%;
  left: 6.03%;
  background-color: var(--color-gray-100);
  filter: blur(65.24px);
}
.painting-icon3 {
  position: absolute;
  height: 100%;
  width: 100%;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  max-width: 100%;
  overflow: hidden;
  max-height: 100%;
  object-fit: cover;
}
.painting-shadow {
  position: relative;
  width: 200px;
  height: 227.59px;
}
.ft {
  position: relative;
  line-height: 28px;
  text-transform: lowercase;
}
.measure-icon {
  position: relative;
  width: 621px;
  height: 19px;
}
.ft-parent {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  gap: var(--gap-3xs);
}
.frame-wrapper,
.painting-shadow-parent {
  display: flex;
  align-items: center;
  justify-content: center;
}
.painting-shadow-parent {
  overflow: hidden;
  flex-direction: column;
  gap: 81px;
}
.frame-wrapper {
  margin: 0 !important;
  position: absolute;
  top: 117px;
  left: 409px;
  flex-direction: row;
  z-index: 1;
}
.bg {
  position: absolute;
  top: 0;
  left: 0;
  background-color: var(--white);
  width: 64px;
  height: 64px;
  cursor: pointer;
}
.button-back,
.icons-navigation-arrow-lef {
  position: absolute;
  top: 16px;
  left: 16px;
  width: 32px;
  height: 32px;
}
.button-back {
  margin: 0 !important;
  top: 25px;
  left: 21px;
  width: 64px;
  height: 64px;
  z-index: 2;
}
.view-in-a-room,
.wall-mockup-1-parent {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.wall-mockup-1-parent {
  background: linear-gradient(rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.2)), #fff;
  gap: var(--gap-3xs);
}
.view-in-a-room {
  background-color: var(--white);
  width: 100%;
  overflow: hidden;
  text-align: left;
  font-size: var(--font-size-5xl);
  color: rgba(101, 99, 95, 0.6);
  font-family: var(--font-karla);
}
